// index.js
import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './mutation-type'
Vue.use(Vuex)


const state = {
    //浏览器是不是电脑
    isPC: null,
    baseUrl: 'http://www.filescloud.cn',
    domain: 'http://omt3mqb30.bkt.clouddn.com/',
    bucket: 'xunzhuresource',
    personId: '',
}

const getters = {
    isPC: function (state) {
        return state.isPC;
    },
    personId: function (state) {
        return state.personId;
    }
}
const actions = {}

const mutations = {
    //记录浏览器是不是电脑
    [types.JUDGE](state, {value}){
        state.isPC = value;
    },
    [types.SETID](state, payload){
        state.personId = payload.personId;
    },
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})



