// router
import Vue from 'vue'
import VueRouter from 'vue-router'
import upLoad from './components/UpLoad.vue'
import index from './Index'
import comment from './components/Comment.vue'

import files from './components/Files.vue'
import readPdf from './components/ReadPdf.vue'
import readImg from './components/readImg.vue'

import store from './store/index'
import VuePreview from 'vue-preview'


// import util from './js/utils'
Vue.use(VuePreview)
Vue.use(VueRouter)

// Vue.prototype.utilHelper = util
const routes = [
    {path: '/', name: "upLoad", component: upLoad},
    {path: '/comment', name: "comment", component: comment},
    {path: '/files', name: "files", component: files},
    {path: '/readPdf', name: "readPdf", component: readPdf},
    {path: '/readImg', name: "readImg", component: readImg},

]


const router = new VueRouter({
    // mode: 'history',
    routes // （缩写）相当于 routes: routes
})


const app = new Vue({
    store,
    router,
    render: h => h(index)
}).$mount('#app')



