<template>
  <div id="app" class="wrapper">
    <header class="main-header">

      <!-- Logo -->
      <a class="logo">
        <span class="logo-mini">云</span>
        <span class="logo-lg"><b>云文档</b>Online</span>
      </a>

      <nav class="navbar navbar-static-top">
        <a href="#" class="sidebar-toggle" data-toggle="offcanvas">
          <span class="sr-only">Toggle <navigatio></navigatio>n</span>
        </a>
      </nav>
    </header>

    <aside class="main-sidebar">
      <section class="sidebar">
        <div class="user-panel">
          <div class="pull-left image">
            <img :src="user.headimgurl" class="img-circle" alt="User Image">
          </div>
          <div class="pull-left info">
            <p>{{user.nickname}}</p>
          </div>
        </div>
        <ul class="sidebar-menu">
          <li :class="{active:activeOne}">
            <a href="javascript:void(0)" @click="upLoadView">
              <i class="glyphicon glyphicon-plus"></i> <span>上传文件</span>
            </a>

          </li>

          <li :class="{active:activeTwo}">
            <a href="javascript:void(0)" @click="filesView">
              <i class="glyphicon glyphicon-file"></i> <span>历史文件</span>
            </a>
          </li>

          <li class="treeview">
            <a href="javascript:void(0)">
              <i class="glyphicon glyphicon-comment"></i> <span>文件评论</span>
            </a>
            <ul class="treeview-menu" style="padding-left:25px">
              <li :class="{active:activeUnReadCom}"><a href="javascript:void(0)"
                                                       @click="unReadcommentView">未读评论 <span
                class="pull-right-container">
              <span class="label label-primary pull-right">{{unReadCount}}</span>
            </span></a>
              </li>
              <li :class="{active:activeAllReadCom}"><a href="javascript:void(0)"
                                                        @click="allReadcommentView">全部评论</a></li>
            </ul>
          </li>
        </ul>
      </section>
    </aside>
    <router-view></router-view>

  </div>
</template>

<style>
  body {
    background-color: #ffffff;
  }
</style>

<script>
  export default {
    data() {
      return {
        activeItem: 1,
        activeOne: true,
        activeTwo: false,
        activeUnReadCom: false,
        activeAllReadCom: false,
        unReadCount: 0,
        user: {}
      }
    },
    components: {},
    mounted(){
      var sUserAgent = navigator.userAgent.toLowerCase();
      var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
      var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
      var bIsMidp = sUserAgent.match(/midp/i) == "midp";
      var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
      var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
      var bIsAndroid = sUserAgent.match(/android/i) == "android";
      var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
      var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
      var value = this.isPC();
      console.log(value);
      this.$store.commit('judge', {value});
      var personId = this.$route.query.id;
      console.log('route', this.$route);
      console.log('personId', personId);
      if (personId) {
        localStorage.setItem('personId', personId);
        this.$store.commit('personId', {personId: personId});
        console.log(localStorage);
      }
      else if (localStorage.getItem('personId')) {
        this.$store.commit('personId', {personId: localStorage.getItem('personId')});
      }
      if (!this.$store.getters.personId) {
        if (this.isWeiXin()) {
          if (this.$route.name == 'readPdf')
            window.location.href = 'http://www.filescloud.cn/oauth/wechat/user?page='
              + this.$route.query.page + '&pdfUrl='
              + this.$route.query.pdfUrl + '&fileId=' + this.$route.query.fileId
          +'&domain=readPdf';
          else if(this.$route.name == 'readImg'){
            window.location.href = 'http://www.filescloud.cn/oauth/wechat/user?imgUrl='
              + this.$route.query.pdfUrl + '&fileId=' + this.$route.query.fileId
              +'&domain=readImg';
          }
          else{
            window.location.href = 'http://www.filescloud.cn/oauth/wechat/user'
          }
        }
        else
        {
          if (this.$route.name == 'readPdf')
            window.location.href = 'http://www.filescloud.cn/oauth/web/user?page='
              + this.$route.query.page + '&pdfUrl='
              + this.$route.query.pdfUrl + '&fileId=' + this.$route.query.fileId
              +'&domain=readPdf';
          else if(this.$route.name == 'readImg'){
            window.location.href = 'http://www.filescloud.cn/oauth/web/user?imgUrl='
              + this.$route.query.pdfUrl + '&fileId=' + this.$route.query.fileId
              +'&domain=readImg';
          }
          else{
            window.location.href = 'http://www.filescloud.cn/oauth/web/user'
          }
        }
      }
      else {
        this.getUnReadCount();
        this.getUserInfo();
      }
      console.log(this.$store.state);
    },
    methods: {
      isWeiXin: function () {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          return true;
        } else {
          return false;
        }
      },
      isPC: function () {
        var userAgentInfo = navigator.userAgent;
        var Agents = ["Android", "iPhone",
          "SymbianOS", "Windows Phone",
          "iPad", "iPod"];
        var flag = true;
        for (var v = 0; v < Agents.length; v++) {
          if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
          }
        }
        return flag;
      },
      upLoadView: function () {
        this.activeOne = true;
        this.activeTwo = false;
        this.activeUnReadCom = false;
        this.activeAllReadCom = false;
        this.$router.push({name: 'upLoad'})
      },
      filesView: function () {
        this.activeOne = false;
        this.activeTwo = true;
        this.activeUnReadCom = false;
        this.activeAllReadCom = false;
        this.$router.push({name: 'files'})
      },
      unReadcommentView: function () {
        this.activeOne = false;
        this.activeTwo = false;
        this.activeAllReadCom = false;
        this.activeUnReadCom = true;
        this.$router.push({name: 'comment', query: {type: 'unread'}})
      },
      allReadcommentView: function () {
        this.activeOne = false;
        this.activeTwo = false;
        this.activeUnReadCom = false;
        this.activeAllReadCom = true;
        this.$router.push({name: 'comment', query: {type: 'all'}})
      }
      , getUnReadCount: function () {
        var socket = io.connect(this.$store.state.baseUrl + '/pushs/subscriber/' + this.$store.getters.personId + '/comments/count');
        var that = this;
        socket.on(this.$store.getters.personId, function (data) {
          that.unReadCount = data.code;
        });
      }
      , getUrlParams: function (name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      }
      , getUserInfo: function () {
        console.log(this.$store.state.personId);
        $.ajax({
          type: 'GET',
          url: this.$store.state.baseUrl + '/oauth/' + this.$store.getters.personId,
          success: this.getUserInfoResult,
          error: this.getUserInfoResult
        })
      },
      getUserInfoResult: function (data) {
        this.user = data;
        console.log(data)
      }
    }
  }
</script>



